.nav{
  max-width: 968px;
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  }
  
  .nav__logo,
  .nav__toggle{
  color: var(--title-color);
  font-weight: var(--font-medium);
  }
  .nav__logo:hover{
  color: var(--first-color);
  }
  
  .nav__toggle{
  font-size: 1.1rem;
  cursor: pointer;
  }
  
  .nav__toggle:hover{
  color: var(--first-color);
  }
  
  /*mobile devices*/
  @media screen and (max-width:767px){
  .nav__menu{
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0,0,0,.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: .3s;
  }
  }
  
  .nav__list{
  grid-template-columns: repeat(3,1fr);
  gap: 2rem;
  }
  
  .nav__item{
  align-items: center;
  }
  .nav__link{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  cursor: pointer;
  text-transform: capitalize;
  }
  
  .nav__link:hover{
  color: var(--first-color);
  }
  .nav__link:hover svg{
  fill: var(--first-color);
  }
  
  .nav__icon{
  font-size: 1.2rem;
  }
  .nav__icon-svg{
  fill:var(--title-color);
  font-size: 1.4rem;
  margin-top: 4px;
  margin-bottom: 2px;
  }
  .nav__close{
  position: absolute;
  right: 1.3rem;
  bottom: .5rem;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--first-color);
  }
  
  .nav__close:hover{
  color: var(--first-color-alt);
  }
  /* show menu */
  .show-menu{
  bottom: 0;
  }
  
  /* Active link */
  .active-link {
  color: var(--first-color);
  }
  
  /* Change background header */
  .scroll-header {
  box-shadow: 0 -1px 4px rgba(0,0,0, .15);
  
  }
  

